.flip-button{
    background-color: transparent;
    perspective: 1000px;
    }
    .flip-button-inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: transform 0.6s;
    transform-style: preserve-3d;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    border-radius: 100px;
    height: 44px;
    }
    
    .flip-button:hover .flip-button-inner {
        transform: rotateY(180deg);
    }
    
    .flip-button-front, .flip-button-back {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    z-index: 100;
    }
    
    
    .flip-button-back {
    color: #7289da;
    transform: rotateY(180deg);
    position: absolute;
    top: 0;
    }
      
    a.link {
    border: none;
    position: absolute;
    bottom: 1rem;
    width: auto;
    right: 1rem;
    }
    .card3 a {
    color: rgba(255,255,255,0.8);
    font-family: Inter, sans-serif;
    display: block;
    font-size: 20px;
    /* padding: 1rem 1.5rem; */
    /* float: left; */
    width: 100%;
    }
      #dark-light-container > div {
          float: left;
          box-sizing: border-box;
          position: relative;
          padding: 2rem;
          width: 50%;
          text-align: center;
    
      }
      .white-container { background: white; }
      .black-container { background: black; }
    
      .card3 {
          box-shadow: none;
          backface-visibility: visible;
          background: transparent;
          font-family: "Poppins", Inter,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Open Sans,Helvetica Neue,sans-serif;
          transform-style: preserve-3d;
          padding: 0;
          height: auto;
          margin: 0;
          width: 100%;
          height: 300px;
          float: left;
          transition: all 0.2s ease-out;
          border: none;
          letter-spacing: 1px;
      }
      .flip, .unflip {
          background: rgba(0,0,0,0.1);
          font-size: 1rem;
          position: absolute;
          top: 1rem;
          right: 1rem;
          padding: 0.5rem 0.75rem;
          border-radius: 100px;
          line-height: 1rem;
          cursor: pointer;
          transition: all 0.1s ease-out;
      }
    .blastoise .flip, .blastoise .unflip {
      background: #1b29ed;
      box-shadow: 0 2px 20px #0b1485;
    }
    .blastoise .inner-card-backface .image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    border-radius: 14px;
    height: 100%;
    transform: rotateY(180deg);
    background-size: auto 102%;
    background-position: -2px -5px;
    }
    .unflip {
    top: auto;
    background: #2d2d62;
    bottom: 1rem;
    }
      .flip:hover {
        background: rgba(0,0,0,0.3);
      }
    
    .blastoise .flip:hover, .blastoise .unflip:hover {
    background: #0f1cc5;
    }
      .inner-card-backface {
          transform: rotateX(0) rotateY(0deg) scale(1) translateZ(-4px);
        border-radius: 14px;
        background: linear-gradient(45deg, #0b0b2a, #0b0b2a);
        position: absolute;
        top: 0;
        color: white;
        padding: 2rem;
        box-sizing: border-box;
        transition: all 0.15s ease-out;
          will-change: transform, filter;
        left: 0;
        width: 100%;
        height: 100%;
      }
      .card3.flipped {
        transform: rotateY(180deg);
      }
      .card3 .flip-inner-card {
        transform: rotateY(180deg);
        position: absolute;
        top: 0;
        padding: 2rem 1.5rem;
        box-sizing: border-box;
        left: 0;
        width: 100%;
        height: 100%;
     }
        .flip-inner-card h3 {
          font-size: 1.25rem;
          margin-top: 0;
        }
        .flip-inner-card p {
          color: rgba(255,255,255,0.6);
          font-weight: 400;
          line-height: 1.5rem;
        }
      .card3 .inner-card {
          font-size: 2rem;
          color: white;
          padding: 1rem 2rem;
          line-height: 3rem;
          will-change: transform, filter;
          float: none;
          background: black;
          background-size: calc(100% + 6px) auto;
          background-position: -3px -3px;
          margin: 0;
          transition: all 0.15s ease-out;
          height: auto;
          border-radius: 14px;
          box-sizing: border-box;
          overflow: hidden;
          display: block;
          margin: 0px auto;
          transform: rotateX(0deg) rotateY(0deg) scale(1);
          top: 0;
          height: 100%;
          filter: drop-shadow(0 15px 15px rgba(0,0,0,0.3));
          font-weight: 500;
          perspective-origin: 0 0;
          letter-spacing: 0;
      }
    
    .card3 .glare {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    transition: all 0.1s ease-out;
    opacity: 0.6;
    pointer-events: none;
    height: 100%;
    border-radius: 14px;
    z-index: 9999;
    mix-blend-mode: hard-light;
    background: radial-gradient(circle at 50% 50%, rgb(199 198 243), transparent); 
    }
    
    .card3 .top-section {
    background: rgba(34, 50, 97, 1);
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 14px 14px 0 0;
    width: 100%;
    height: 35%;
    z-index: 99;
    }
    
    .user-2 .top-section {
    background: white;
    }
    .card3 .user-icon {
    position: absolute;
    left: calc(50% - 42.66666666666667px);
    top: calc(35% - 48px);
    z-index: 99;
    width: 96px;
    height: 96px;
    box-shadow: 0 4px 9px rgba(0,0,0,0.2);
    border-radius: 100px;
    background-color: rgba(255,255,255,0.2);
    }
    .card3 .user-icon img {
    position: absolute;
    left: calc(50% - 32px);
    top: calc(35% - 18px);
    width: 64px;
    height: 64px;
    transform: scale(1);
    transition: all 0.1s ease-out;
    }
    
    .card3 .user-icon img:hover {
    transform: scale(1.08);
    }
    .card3 .glare {
    opacity: 0;
    }
    .card3.animated .glare {
    opacity: 0.3;
    }
    
    .card3.animated.user .glare {
    opacity: 0.15;
    }
    
    .card3 .name {
    color: white;
    font-size: 1.5rem;
    }
    
    .card3 .bottom-section {
    position: absolute;
    top: 35%;
    left: 0;
    background: rgba(34, 50, 97, 0.95);
    width: 100%;
    height: 65%;
    box-sizing: border-box;
    padding-top: 64px;
    text-align: center;
    }
    
    .bottom-section .area {
    font-size: 1rem;
    opacity: 1;
    display: block;
    line-height: 1rem;
    }
    .buttons {
    float: left;
    width:  calc(100% - 2rem);
    position: absolute;
    bottom: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    left: 1rem;
    }
    .flip-button-back > div{
        height: 44px;
        border: none;
        width: auto;
        background: white;
        font-size: 12px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #7289da;
        line-height: 1rem;
        transition: all 0.1s ease-out;
        transform: scale(1);
        padding: 10px 30px;
        border-radius: 100px;
        text-decoration: none;
        text-transform: uppercase;
    }
    .flip-button-front > button{
        height: 44px;
        border: none;
        width: auto;
        background: #7289da;
        font-size: 0.875rem;
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        line-height: 1rem;
        transition: all 0.1s ease-out;
        transform: scale(1);
        padding: 10px 30px;
        border-radius: 100px;
        text-decoration: none;
        text-transform: uppercase;
    }
    .buttons > a{    
      border: none;
      width: auto;
      background: #7289da;
      padding: 0;
      font-size: 0.875rem;
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      line-height: 1rem;
      transition: all 0.1s ease-out;
      transform: scale(1);
      padding: 0.75rem 1rem;
      border-radius: 100px;
      text-decoration: none;
    }
    .buttons .main {
    background: #3d26e7;
    }
    .buttons > .main:hover {
    background: #614df1;
    }
    .buttons > a:hover {
    background: #494989;
    }
    .flip-button-front > button:hover {
        background: #494989;
        }
    .buttons .message svg {
    margin: 0 0.5rem 0 0;
    }
    .buttons .message {
    margin: 0;
    }
      @media screen and (max-width: 1000px) {
          #dark-light-container > div {
              width: 100%;
          }
      }