.wrapper{position:absolute;z-index:100;top:0;left:0;width:100%;transition:all 1s ease}.header{display:flex;justify-content:space-between;align-items:center}.sticky{background:#282832;position:fixed;top:0}.hidden{position:fixed;top:-100%}.menu{display:flex}.menu>li{position:relative}.menu>li>a,.menu>li span>a{display:block;color:#fff;font-size:1.3rem;line-height:1.4;padding:4rem 2rem;transition:all .3s ease}.menu>li>span>a:after{content:"";font-family:"Line Awesome Free";font-weight:700;margin-left:7px}.menu>li:hover ul{visibility:visible;opacity:1;transform:translateY(0);pointer-events:visible}.menu>li ul{display:flex;flex-direction:column;position:absolute;width:25rem;background-color:#fff;box-shadow:0 .2rem .8rem rgba(40,40,50,.1);visibility:hidden;opacity:0;pointer-events:none;transform:translateY(50px);transition:all .5s ease}.menu>li ul li:not(:last-of-type){border-bottom:1px solid #ddd}.menu>li ul a{color:#000;font-size:1rem;letter-spacing:1px;font-weight:600;display:block;padding:2rem;transition:all .3s ease}.menu>li ul a:hover{background:#ddd}.btn{display:flex;flex-shrink:0;align-items:center;justify-content:center;text-decoration:none;text-transform:uppercase;font-size:1.1rem;line-height:1.1;font-weight:600;letter-spacing:1px;transition:all .3s ease;border:2px solid #fff;background:rgba(0,0,0,0);border-color:#fff;color:#fff;padding:1.5rem;font-size:22px;border-radius:50%}.btn_desktop{display:flex;gap:20px}.btn_mobile{display:none;padding:30px 0}@media(max-width: 992px){.menu{display:none}.btn_desktop{display:none}.btn_mobile{display:flex}}