  height: fit-content;
  display: flex;
  justify-content: center;
  align-items: start;
  box-shadow: inset 0 0 0 2000px rgba(40, 40, 50, 0.8);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

@keyframes flickerAnimation {
  0% {
