*, *:before, *:after {
  box-sizing: border-box;
}
.scroll-container{
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  overflow-x: scroll;
  max-width: 80vw;
}
.scroll-container > * {
  flex-basis: calc(33.333333% - 3rem);
  margin: 30px;
}
.scroll-container::-webkit-scrollbar {
  width: 16px;
}

div:has(.bosscard)::-webkit-scrollbar-track {
  background-color: #e4e4e4;
  border-radius: 100px;
  margin: 50px;
}

div:has(.bosscard)::-webkit-scrollbar-thumb {
  background-color: #62C8E8;
  border-radius: 100px;

}
.bosscard:first-of-type{
  margin-left: 0;
}
.scroll {
	width: 60px;
	height: 60px;
  margin-top: 30px;
	border: 2px solid white;
	border-radius: 50%;
	position: relative;
	animation: down 1.5s infinite;
	-webkit-animation: down 1.5s infinite;
}
.scroll::before{
  content: '';
  position: absolute;
  top: 15px;
  left: 18px;
  width: 18px;
  height: 18px;
  border-left: 2px solid white;
  border-bottom: 2px solid white;
  transform: rotate(-45deg);
}
.scroll:hover::before{
  border-left: 2px solid rgb(34, 50, 97) !important;
  border-bottom: 2px solid rgb(34, 50, 97) !important;
}

@keyframes down {
	0% {
		transform: translate(0);
	}
	20% {
		transform: translateY(15px);
	}
	40% {
		transform: translate(0);
	}
}

@-webkit-keyframes down {
	0% {
		transform: translate(0);
	}
	20% {
		transform: translateY(15px);
	}
	40% {
		transform: translate(0);
	}
}

@keyframes updown {
	0% {
		transform: translateY(5px);
	}
  50% {
		transform: translateY(-5px);
	}
  100% {
    transform: translateY(5px);
  }
}

.updown{
  animation: updown 1.5s infinite;
  -webkit-animation: updown 1.5s infinite;
}
@-webkit-keyframes updown {
	0% {
		transform: translateY(5px);
	}
  50% {
		transform: translateY(-5px);
	}
  100% {
    transform: translateY(5px);
  }
}


