.clash-card__unit-stats--giant .one-third {
  border-right: 1px solid #de7b09;
}

.clash-card__unit-stats--goblin {
  background: #82BB30;
}
.clash-card__unit-stats--goblin .one-third {
  border-right: 1px solid #71a32a;
}

.clash-card__unit-stats--wizard {
  background: #4FACFF;
}
.clash-card__unit-stats--wizard .one-third {
  border-right: 1px solid #309eff;
}

.clash-card__unit-stats {
  color: white;
  font-weight: 700;
  border-bottom-left-radius: 14px;
  border-bottom-right-radius: 14px;
}
.clash-card__unit-stats .one-third {
  width: 33%;
  float: left;
  padding: 20px 15px;
}
.clash-card__unit-stats sup {
  position: absolute;
  bottom: 4px;
  font-size: 45%;
  margin-left: 2px;
}
.clash-card__unit-stats .stat {
  position: relative;
  font-size: 24px;
  margin-bottom: 10px;
}
.clash-card__unit-stats .stat-value {
  text-transform: uppercase;
  font-weight: 400;
  font-size: 12px;
}
.clash-card__unit-stats .no-border {
  border-right: none;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}

.slick-prev {
  left: 100px;
  z-index: 999;
}

.slick-next {
  right: 100px;
  z-index: 999;
}
.form-group .user-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 99;
  width: 10%;
  height: 67px;
  box-shadow: 0 4px 9px rgb(0 0 0 / 20%);
  border-radius: 50%;
  background-color: rgba(255,255,255,0.2);
  margin-left: 50px;
  min-width: 67px;  
  }
.form-group  .user-icon img {
  width: 75%;
  
  transform: scale(1);
  transition: all 0.1s ease-out;
  }
  
.form-group .user-icon img:hover {
  transform: scale(1.08);
}

.apply-form{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.form-label{
  /* background: -webkit-linear-gradient(90deg, rgba(255,255,255,1) 5%, rgba(98,200,232,1) 39%, rgba(255,255,255,1) 95%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; */
  color: white;
  font-family: "Poppins";
  display: inline-block;
  margin: 30px 0px;
}
.form-control:focus, .form-control:active{
  background-color: #223261;
  color: #62C8E8;
}
.btn-primary{
  width: 250px !important;
  text-transform: uppercase;
  cursor: pointer !important;
}
.form-select{
  cursor: default !important;
}
@media screen and (max-width: 768px){
  .user-icon{
    margin-left: 10px;
    min-width: 50px;
    height: 50px;
  }
  .apply-form{
    width: 100% !important;
  }
  .form-label{
    font-size: 16px;
    margin-bottom: 10px;
  }
  .form-control{
    font-size: 12px;
  }
}

.form-control, .form-select, .btn-primary{
  position: relative;
  width: 100%;
  margin: 0 0 50px;
  padding: 24px 33px;
  font-family: inherit;
  font-size: 15px;
  font-weight: inherit;
  line-height: calc(63px - (48px - 2px));
  -webkit-clip-path: polygon(0 0,calc(100% - 20px) 0,100% 20px,100% 100%,20px 100%,0 calc(100% - 20px));
          clip-path: polygon(0 0,calc(100% - 20px) 0,100% 20px,100% 100%,20px 100%,0 calc(100% - 20px));
  color: white;
  border: 1px solid #62C8E8;
  background-color: #223261;
  border-radius: 0;
  outline: 0;
  cursor: text;
  /* -webkit-appearance: none; */
  box-sizing: border-box;
  transition: border-color .2s ease-in-out;
  -webkit-filter: drop-shadow(0 3px 7px rgba(14, 8, 18, .44));
  filter: drop-shadow(0 3px 7px rgba(14, 8, 18, .44));
}
.form-group{
  width: 80%;
}
.form-control{
  margin: 0px;
}
